<template>
  <div class="sideBar">

    <v-navigation-drawer class="mx-auto"
                         width="200"
                         :height="height"
                         :mini-variant="miniVariant"
                         :permanent='permanent'>

      <v-divider></v-divider>
      <v-list :style="{height: height,overflowY: 'auto'}">
        <sideBar-item :dense="false"
                      :routes="menuList"
                      icon-show></sideBar-item>
      </v-list>
    </v-navigation-drawer>
  </div>

</template>
<script>
import sideBarItem from './sideBarItem';
import menuList from '@/router/allRouters'
export default {
  components: { sideBarItem },
  data () {
    return {
      permanent: true,
    }
  },
  computed: {
    menuList () {
      // return this.$store.state.sideMenu
      return menuList
    },
    miniVariant () {
      return this.$store.state.miniVariant
    },
    height () {
      return this.$store.state.calcHeight
    }
  },
  mounted () {
  }
}
</script>
<style scoped>
.sideMenu /deep/ .v-list-item__title {
  flex: initial;
}
.sideMenu .active {
  font-weight: bold;
}
.sideBar /deep/ .v-navigation-drawer__content {
  overflow: hidden;
}
</style>